import {hasSomeParentOfClass} from '../utils/dom';

class Job {
  constructor() {
    this.addListeners();
  }

  addListeners() {
    document.addEventListener('click', (evt) => {
      const jobElement = hasSomeParentOfClass(evt.target, `job`);

      if (jobElement) {
        this.switchJobSpoiler(jobElement);
      }
    })
  }

  switchJobSpoiler(jobElement) {
    jobElement.classList.toggle('job--open');
  }
}

export default Job;
